body{
  text-align: center; 


  overflow-x: hidden;
  color: #3a7bd5;
 
  background: #4AC29A;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #BDFFF3, #4AC29A);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #BDFFF3, #4AC29A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
background-image: url("https://www.transparenttextures.com/patterns/inspiration-geometry.png"),  linear-gradient(to right, dodgerblue, dodgerblue);
  
  

}

.login-img{
  background-image: url("../src/images/login-img.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  height: 100vh;
  position: relative;
  z-index: -2;
}

.overlay{
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: rgb(97, 97, 81);
  opacity: 0.5;
  z-index: -1;
  mix-blend-mode:saturation;
}

.login-container{
  position: relative !important;
  height: 100vh;

  
  /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
  
}

.posts-container{
  flex: 1;
}

.login{
background-color: whitesmoke;
padding: 25px;
width: 50%;
border-radius: 10px;
margin: 15% auto;
}





