textarea{
  padding: 10px;
  border-radius: 10px;
  border: none;
  width: 100%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  resize: none;
  border: 1px silver solid;
}


.comment-content, .post-content{
  word-wrap: break-word;
}

.post-container button{
  padding: 10px;
  border-radius: 10px;
  border: none;
 
  margin: 25px auto;
  float: right;
  vertical-align: bottom;
}

.post-container, .comment-container{
  padding: 25px;
  background-color: whitesmoke;
  color: black;
  text-align: left;
  margin: 25px auto;
  border-radius: 15px;
  border: 3px dodgerblue solid;
  font-family: 'Encode Sans', sans-serif;
}

.comment-container{
  width: 90%;
}


.post-container h2{
display: inline-block;
vertical-align: bottom;
margin-left: 25px;
font-family: 'Encode Sans', sans-serif;

}
.post-container p{
 font-size: 1.2rem;
 word-wrap: break-word;
 color: black;

}

.post-container button{
  border: 2px dodgerblue  solid;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
  
}

.post-container button:hover{
  background-color: dodgerblue;
  transition: ease-in;
  transition-duration: 150ms;
}


.profile-details{
  border-bottom:  2px dodgerblue solid;
  margin-bottom: 25px;
  padding: 25px;
}

.profile-name{

  background-color: dodgerblue;
  color: whitesmoke;
  padding: 7.5px ;
  border-radius: 15px;
 
}

.profile-pic{
  width: 50px;
  border-radius: 50%;
}

.comment-input{
  text-align: left;
  
}

.comment-btn{
  border-radius: none;
  border: none;
  cursor: pointer;
}

.comment-input input{
  width: 100%;
  padding: 10px;
}

.login-input{
  display:block;
  margin: 10px auto;
  padding: 10px;
  border-radius: 5px;
  border:none;
}















.posts-heading-container{

  border-radius: 10px;
  position: relative;
  margin-bottom: 50px;
}

.posts-heading{
  text-align: center;
  color: whitesmoke;
  text-decoration: underline;
  padding: 15px;
  border-radius: 15px;
  background: #00d2ff;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3a7bd5, #00d2ff);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3a7bd5, #00d2ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  top: 0;
  left: 0;
  width: 100%;
}

.header-img{
  width: 100px;
  position: absolute;
 top: 50%;
 left: 75%;
  background-color: white;
  padding: 10px;
  border-radius: 50%;
  border: 1px solid dodgerblue;
  transform: translate(-50%,-50%);
 }

.posts-slogan{
  margin-bottom: 50px;
  padding: 10px;
  font-family: 'Pacifico', cursive;
  text-align: left;
}



.fade-in-fwd {
	-webkit-animation: fade-in-fwd 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in-fwd 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes fade-in-fwd {
  0% {
    -webkit-transform: translateZ(-80px);
            transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fade-in-fwd {
  0% {
    -webkit-transform: translateZ(-80px);
            transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}


@media only screen and (max-width: 600px) {

  
  .post-container{
    text-align: center;
  }

  .posts-heading{
    text-align: left;
  }

  .profile-name{
    display: block;
  }
}